@import "vars";
@import "logo";
@import "mixins";
@import "submenu";

header {
	box-sizing: border-box;
	height: $height; }

.header-flex {
	height: 100%;
	border-bottom: $border-width solid $color-grey;
	background-color: $color-white;
	@include flex;
	@include center();
	@include space-between();
	.left-header {
		@include flex;
		@include center();
		&__logo {
			@include flex;
			@include center(); } } }

h1 {
	color: $color-h1;
	font-size: $f-size-14;
	text-transform: uppercase;
	@include scaleX($scaleX);
	line-height: $l-height-26;
	@include border-bottom($border-width, $color-shape); }

.search {
	@include flex();
	@include center();
	opacity: 0.5;
	&__str {
		@include border($border-width, $border-color);
		background-color: $color-white;
		color: $plh-color;
		font-size: $f-size-14;
		font-weight: $f-weight-300;
		line-height: $l-height-32;
		padding: 0 160px 0 16px;
		&:focus {
			outline: none; } }
	&__btn {
		@include border($border-width, $border-color);
		background-color: $color-white;
		@include border-top-right-radius(5px);
		@include border-bottom-right-radius(5px);
		color: $plh-color;
		font-size: $f-size-14;
		font-weight: $f-weight-300;
		line-height: $l-height-32;
		padding: 0 8px;
		@include transition-bg-color-color($time-03);
		cursor: pointer;
		&:focus {
			outline: none; }
		&:active {
			background-color: $plh-color;
			color: $color-white; } } }

.right-header {
	@include flex();
	@include center(); }

.link-cart {
	padding: 50px 40px 30px 40px; }

.ellips {
	position: absolute;
	top: 40px;
	left: 62px;
	@include border-radius(50%);
	background-color: $color-brand-pink;
	@include flex();
	@include center();
	span {
		padding: 3px 6px;
		color: $color-white;
		font-size: $f-size-12;
		font-weight: $f-weight-700;
		line-height: $l-height-16; } }

.position {
	@include flex();
	@include space-between();
	@include center();
	padding: 14px 0;
	@include border-bottom($border-width, #ededed);
	&:nth-child(1) {
		padding-top: 20px; }
	a {
		@include flex();
		@include space-between();
		&:hover .foto {
			@include brightness(90%); }
		&:hover .foto-text p {
			color: $color-brand-pink; }
		&:active .foto-text p {
			color: $color-brand-text; } } }

.foto {
	width: 72px;
	height: 85px;
	background-color: $color-grey;
	margin-right: 17px;
	@include transition-color(0.5s);
	img {
		width: 100%;
		height: 100%; } }


.foto-text {
	@include flex();
	@include column();
	@include space-around();
	@include transition-color(0.5s);
	p {
		color: $color-brand-text;
		font-size: $f-size-12;
		font-weight: $f-weight-700;
		text-transform: uppercase;
		@include transition-color(0.5s); }
	span {
		color: $color-brand-pink;
		font-size: $f-size-12;
		font-weight: $f-weight-400; } }

.stars-fa-fw {
	@include flex();
	width: 56px;
	color: $color-yellow;
	font-size: $f-size-12; }

.cross {
	background-color: $color-cross;
	width: 20px;
	height: 21px;
	@include border-radius(10px);
	text-align: center;
	color: $color-white;
	@include transition-bg-color-color($time-03);
	cursor: pointer;
	&:active {
		color: $color-cross;
		background-color: $color-white; } }

.total {
	@include flex();
	@include space-between();
	margin: 17px 0;
	span {
		color: $color-brand-text;
		font-size: $f-size-16;
		font-weight: $f-weight-700; } }

.cart-btn {
	@include flex();
	@include column();
	@include space-between();
	margin-bottom: 17px;
	button {
		@include flex();
		@include center();
		@include space-around();
		padding: 17px 25px;
		background-color: $color-white;
		@include border($border-width, $border-color);
		color: $color-black-grey;
		font-size: $f-size-14;
		font-weight: $f-weight-700;
		cursor: pointer;
		text-transform: uppercase;
		@include transition-bg-color-color($time-03);
		&:first-child {
			margin: 15px 0; }
		&:focus {
			outline: none; }
		&:hover {
			@include border($border-width, $color-brand-pink);
			color: $color-brand-pink; }
		&:active {
			background-color: $color-brand-pink;
			color: $color-white; } } }

.account {
	@include flex();
	@include center();
	@include space-between();
	padding: 10px 20px;
	background-color: $color-brand-pink;
	color: $color-white;
	font-size: 15px;
	font-weight: 400;
	line-height: 24px;
	i {
		margin-left: 5px; } }
