@import "vars";
@import "mixins";

.main-index__offers {
    @include flex();
    margin-bottom: 100px;
    &_content {
        width: 50%;
        &:first-child {
            margin-right: 20px; } } }

.offers-content__img {
    position: relative;
    @include transition-color($time-03);
    &:hover {
        @include transform-scale($scale-05); }
    &:first-child {
        margin-bottom: 20px; }
    img {
        width: 100%; } }


.offers-content__text {
        text-transform: uppercase;
        font-weight: $f-weight-900;
        background-color: $color-white;
        padding: 10px;
        z-index: 1;
        position: absolute;
        top: 25px;
        left: 0px;
        @include transition-color($time-03);
        &:hover {
            background-color: $color-brand-pink; }
        &:hover p, &:hover span {
            color: $color-white; }
        p {
            font-size: $f-size-18;
            color: $color-brand-text;
            @include transition-color($time-03); }
        span {
            font-size: $f-size-30;
            color: $color-brand-pink;
            @include transition-color($time-03); } }

