@mixin border-radius($radius) {
	-webkit-border-radius: $radius;
	-moz-border-radius: $radius;
	-ms-border-radius: $radius;
	border-radius: $radius; }

@mixin border-bottom-left-radius($radius) {
	-webkit-border-bottom-left-radius: $radius;
	-moz-border-bottom-left-radius: $radius;
	-ms-border-bottom-left-radius: $radius;
	border-bottom-left-radius: $radius; }

@mixin border-bottom-right-radius($radius) {
	-webkit-border-bottom-right-radius: $radius;
	-moz-border-bottom-right-radius: $radius;
	-ms-border-bottom-right-radius: $radius;
	border-bottom-right-radius: $radius; }

@mixin border-top-left-radius($radius) {
	-webkit-border-top-left-radius: $radius;
	-moz-border-top-left-radius: $radius;
	-ms-border-top-left-radius: $radius;
	border-top-left-radius: $radius; }

@mixin border-top-right-radius($radius) {
	-webkit-border-top-right-radius: $radius;
	-moz-border-top-right-radius: $radius;
	-ms-border-top-right-radius: $radius;
	border-top-right-radius: $radius; }

@mixin border($border-width, $border-color) {
	border: $border-width solid $border-color; }

@mixin border-top($border-width, $border-color) {
	border-top: $border-width solid $border-color; }

@mixin border-left($border-width, $border-color) {
	border-left: $border-width solid $border-color; }

@mixin border-bottom($border-width, $border-color) {
	border-bottom: $border-width solid $border-color; }

@mixin rotate($triangle-rotate) {
	-webkit-transform: rotate($triangle-rotate);
	-ms-transform: rotate ($triangle-rotate);
	transform: rotate ($triangle-rotate); }

@mixin box-shadow($top, $right, $bottom, $left, $color-box-shadow) {
	-webkit-box-shadow: $top $right $bottom $left $color-box-shadow;
	box-shadow: $top $right $bottom $left $color-box-shadow; }

@mixin liner-gradient($color1-grad-submenu, $color2-grad-submenu) {
	background: -webkit-gradient(linear, left top, left bottom, from($color1-grad-submenu), to($color2-grad-submenu));
	background: -webkit-linear-gradient(top, $color1-grad-submenu 0%, $color2-grad-submenu 100%);
	background: -o-linear-gradient(top, $color1-grad-submenu 0%, $color2-grad-submenu 100%);
	background: linear-gradient(180deg, $color1-grad-submenu 0%, $color2-grad-submenu 100%); }

@mixin flex() {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex; }

@mixin center() {
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center; }

@mixin space-between() {
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between; }

@mixin content-center() {
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center; }

@mixin scaleX($scaleX) {
	-webkit-transform: scaleX($scaleX);
	-ms-transform: scaleX($scaleX);
	transform: scaleX($scaleX); }

@mixin column() {
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-ms-flex-direction: column;
	flex-direction: column; }

@mixin transition-bg-color-color($time-03) {
	-webkit-transition: background-color $time-03 ease-out, color $time-03 ease-out, border-color $time-03 ease-out;
	-o-transition: background-color $time-03 ease-out, color $time-03 ease-out, border-color $time-03 ease-out;
	transition: background-color $time-03 ease-out, color $time-03 ease-out, border-color $time-03 ease-out; }

@mixin space-around() {
	-ms-flex-pack: distribute;
	justify-content: space-around; }

@mixin brightness($bright) {
	-webkit-filter: brightness($bright);
	filter: brightness($bright); }

@mixin transition-color($time-03) {
	-webkit-transition: $time-03 ease;
	-o-transition: $time-03 ease;
	transition: $time-03 ease; }

@mixin transition-submenu($time-03) {
	-webkit-transition: all $time-03 ease;
	-o-transition: all $time-03 ease;
	transition: all $time-03 ease; }

@mixin transform-scale($scale-05) {
	-webkit-transform: scale($scale-05);
	-ms-transform: scale($scale-05);
	transform: scale($scale-05); }

@mixin flex-wrap() {
	-ms-flex-wrap: wrap;
	flex-wrap: wrap; }
